.container{
    width: 100%;
    padding: 0 20px;
}
@media screen and (min-width:320px){
    .container{
        max-width: 320px;
        margin: 0 auto;
}
}
@media screen and (min-width:768px){
    .container{
        max-width: 768px;
        padding: 0 40px;
    }
}
@media screen and (min-width:1316px){
    .container{
        max-width: 1316px;
    }
}